*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}body{margin:0 0 3rem 0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1rem;background-color:#fdf5ff;color:#636363}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4{color:#292929;font-weight:700}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}.section{padding:2rem 0 3rem}.sectionTitle{font-size:1.5rem}.sectionSubtitle{display:block;font-size:.9rem;margin-bottom:2rem}.sectionTitle,.sectionSubtitle{text-align:center}.container{max-width:768px;margin-left:1.5rem;margin-right:1.5rem}.grid{display:grid;gap:1.5rem}.homeMain{width:100vw;height:100vh;display:flex;align-items:center;justify-content:center;text-align:center;background-color:#f8f8f8}.homeMain .wrapper{width:60%;height:70vh;display:flex;flex-direction:column;justify-content:space-between;border-radius:10px;box-shadow:0px 8px 8px 0px rgba(0,0,0,0.2);padding:20px;background-color:#f8f8f8;position:relative}.homeMain .wrapper .gitIcon{font-size:5rem;margin:0 auto;color:#a800a8}.homeMain .wrapper .description{width:60%;margin:0 auto}@media screen and (max-width: 764px){.homeMain .wrapper{width:90%;height:90vh}.homeMain .wrapper .description{width:90%;margin:0 auto}}form{display:flex;flex-direction:column;align-items:center;justify-content:center;margin-top:-50px}form label{font-size:1.25rem;font-weight:bold}form input{width:50%;height:25px;margin:3% 0;background-color:#f8f8f8;color:#000;text-align:center;font-size:1.25rem;border:none;border-bottom:2px solid #a800a8}form input::placeholder{text-align:center}form input:focus{outline:none}form button{width:170px;height:40px;font-size:1.25rem;font-weight:bold;background-color:#a800a8;color:white;border:none;border-radius:8px;box-shadow:0px 8px 8px 0px rgba(0,0,0,0.2);cursor:pointer;transition:.5s}form button:hover{box-shadow:0px 10px 8px 0px rgba(0,0,0,0.3);background-color:#5e005e;color:#f0f}form button:disabled{color:rgba(180,180,180,0.301);cursor:progress}@media screen and (max-width: 764px){form label{font-size:1.2rem;font-weight:bold}form input{width:70%;font-size:1rem}}.footerSection{display:flex;align-items:center;justify-content:center}.footerSection img{width:30px;height:30px;border-radius:50%;margin-left:2%}
