*{margin:0;padding:0;box-sizing:border-box}html{scroll-behavior:smooth}body{margin:0 0 3rem 0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1rem;background-color:#fdf5ff;color:#636363}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4{color:#292929;font-weight:700}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}.section{padding:2rem 0 3rem}.sectionTitle{font-size:1.5rem}.sectionSubtitle{display:block;font-size:.9rem;margin-bottom:2rem}.sectionTitle,.sectionSubtitle{text-align:center}.container{max-width:768px;margin-left:1.5rem;margin-right:1.5rem}.grid{display:grid;gap:1.5rem}.header{width:80%;margin:0 auto;padding:3.5rem;display:flex;flex-direction:column;align-items:center;justify-content:center}@media screen and (max-width: 820px){.header{width:98%;padding:3.5rem 1rem}}.header img{width:200px;height:200px;object-fit:contain;border-radius:50%;border:8px solid #fdf5ff;margin-bottom:1rem;box-shadow:0px 4px 4px 0px rgba(0,0,0,0.15)}@media screen and (max-width: 640px){.header .githubName{font-size:1.5rem}}.header p{width:60%;text-align:center;margin:2% 0}@media screen and (max-width: 820px){.header p{width:70%;margin:5% 0}}@media screen and (max-width: 640px){.header p{width:95%;margin:5% 0}}.header .info{display:flex;align-items:center;justify-content:center;font-size:1rem;font-weight:bold}.header .info .infoIcon{font-size:1rem;margin:0 5px}.header .info .infoIcon:last-child{margin-left:20px}.header .repoInfo{display:flex;align-items:center;justify-content:space-between;text-align:center}.header .repoInfo .circleShape{margin-top:1rem !important}.header .repoInfo .number{width:40px;height:40px;background-color:#a800a8;color:white;border-radius:50%;display:flex;justify-content:center;align-items:center;margin:0 auto;font-weight:bold}.header .repoInfo .center{margin:0 20px}.backIcon{position:fixed;top:20px;left:20px;font-size:1.5rem;color:black;z-index:100}.headerTop{width:80%;display:flex;justify-content:space-evenly;align-items:center}.headerSocials{display:grid;grid-template-columns:max-content;row-gap:1rem}.headerSocials .socialIcons{color:#a800a8}
